/**  */
const getRandomArray = (length = 20, min = 0, max = 100) => {
  if (!Number.isSafeInteger(length)) { throw new TypeError(`length${length}`); }
  if (!Number.isSafeInteger(min)) { throw new TypeError(`min${min}`); }
  if (!Number.isSafeInteger(max)) { throw new TypeError(`max${max}`); }
  if (max < min) { throw new TypeError('max < min'); }
  const container = [];
  for (let index = 0; index < length; index += 1) {
    const value = Math.trunc((max + 1 - min) * Math.random()) + min;
    container.push(value);
  }
  return container;
};

export { getRandomArray };
