const radixSort = (array) => {
  if (!(array instanceof Array)) { throw new TypeError(`${array} Array`); }
  // 10
  const container = [[], [], [], [], [], [], [], [], [], []];
  // 
  let max = array[0];
  array.forEach((item) => {
    if (item > max) { max = item; }
  });
  // 
  let digit = 0;
  while (max > 0) {
    digit += 1;
    max = Math.trunc(max / 10);
  }
  // 2233
  for (let time = 0; time < digit; time += 1) {
    // 
    array.forEach((item) => {
      const index = Math.trunc(item / (10 ** time)) % 10;
      container[index].push(item);
    });
    // 
    array.splice(0, array.length);
    container.forEach((team) => {
      array.push(...team);
      team.splice(0, team.length);
    });
  }
  return array;
};

export default radixSort;
